html, body {
    background-color: rgb(15, 14, 14);
}

.movies-section {
    text-align: center;
    padding: 50px;
}

.movies-section h1 {
    font-size: 2.5rem;
    margin-bottom: 30px;
    color: white;
}

.info-container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
    margin-bottom: 30px;
}

.info h2 {
    font-size: 2rem;
    margin: 0;
    color: white;
}

.info-container hr {
    height: 2px;
    width: 50px;
    background-color: #ffffff;
    border: none;
    margin: 0px;
}

.image-container img {
    width: 80%;
    height: auto;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    cursor: pointer;
}

.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}

